import React, { Component } from 'react';
import { FolderTreeNode } from "./folder_tree_node";

export default class FolderPicker extends Component {
    constructor(props) {
        super(props);
        
        this.handleOnNewFolder = this.handleOnNewFolder.bind(this);
        this.handleOnKeyDown = this.handleOnKeyDown.bind(this);
        this.state = {
            selected:this.props.selected
        };
    }
    
    render() {
        return(
            <fieldset ref="pickerFieldset">
                <legend>Choose Parent Folder</legend>
                <FolderTreeNode key={this.props.tree.path} tree={this.props.tree} selected={this.state.selected}/>
                <div className="w3-margin-top">
                    <label htmlFor="newfolder" className="sr-only">Folder Name</label>
                    <input type="text" placeholder="New Folder Name" id="newfolder" ref="folderInput" onChange={this.handleOnKeyDown}/>
                    <input type="button" value="Create Folder" onClick={this.handleOnNewFolder}/>
                </div>
            </fieldset>
        );
    }
    
    
    handleOnKeyDown(e) {
        e.target.value = e.target.value.replace(this.props.regex,"").toLowerCase();
    }
    
    handleOnNewFolder() {
        let folderName = this.refs.folderInput.value,
            fieldset = this.refs.pickerFieldset;
        
        if(folderName !== "" && fieldset) {
            
            const elements = fieldset.querySelectorAll('input[name="path"]'),
                  parentFolder = this._findSelectedFolder(elements),
                  parentTreeNode = this._findTreeNode(parentFolder.value, this.props.tree);
           
            if(parentFolder && parentTreeNode) {
                let newTreeNode = {
                    children:[],
                    id:null,
                    leaf:false,
                    name:folderName,
                    path:parentTreeNode.path + folderName + "/",
                    root:false
                };
                
                parentTreeNode.children.push(newTreeNode);
                
                this.setState({
                   selected: newTreeNode.path
                });
                
                this.refs.folderInput.value = "";
            } else {
                console.log("parent item not found");
                console.log(parentFolder);
                console.log(parentTreeNode);
            }
        } else {
            console.log("no folder name");
        }
    }
    
    _findSelectedFolder(elements) {
        for(let i=0; i<elements.length; i++) {
            if(elements[i].name === "path" && elements[i].type === "radio" && elements[i].checked) {
                return elements[i];
            }
        }
    }
    
    _findTreeNode(needle, haystack) {
        if(!haystack.leaf) {
            if(haystack.path === needle) {
                return haystack;
            }
            
            for(let i=0; i<haystack.children.length; i++) {
                let child = this._findTreeNode(needle, haystack.children[i]);
                
                if(child) {
                    return child;
                }
            }
        }
    }
}


