const _properties = {
    baseUrl:`https://${window.location.hostname}`,
    projectId:null,
    pathRegex:/[^a-zA-Z0-9-_]/g
};

const listeners = {};

let _loaded = false;

exports.load = (props) => {                 
    //set only defined properties
    for(let n in _properties) {
        if(props[n]) {
            _properties[n] = props[n];
        }
    }
    
    //set status to loaded
    _loaded = true;
    
    //notify any listening objects
    for(let i=0; i<listeners.load.length; i++) {
        listeners.load[i]();
    }
};

exports.get = (key) => {                    
    return _properties[key];
};

exports.isLoaded = () => {
    return _loaded;  
};

exports.on = (event, callback) => {
    if(typeof event === "string" && typeof callback === "function") {
        if(!listeners[event]) listeners[event] = [];
        listeners[event].push(callback);
        
        if(event === "load" && _loaded) {
            callback();
        }
    }
};


