import React, { Component } from 'react';

export class DeleteForm extends Component {
    constructor(props) {
        super(props);
        
        this.handleOnSubmit = this.handleOnSubmit.bind(this);
        this.fileName = this.props.data.path.substr(this.props.data.path.lastIndexOf("/")+1);
    }
    
    render() {
  
        return(
            <form onSubmit={this.handleOnSubmit} className="w3-container w3-margin-top branding-form" action="/branding/files/delete/{this.props.date.id}"> 
                <div className="w3-margin-top w3-panel w3-border w3-border-red">
                    <p className="w3-text-red">
                        Type "{this.fileName}" to permenantly delete this file.
                    </p>
                </div>
                <div className="w3-margin-top">
                    <label htmlFor="name">Confirm File Name</label>
                    <input id="name" name="name" className="w3-input" type="text" placeholder={this.fileName}/>
                </div>
                <div className="w3-margin-top">
                    <input type="submit" value="Delete" className="w3-button w3-red" required={true}/>
                </div>
            </form>
        );
    }  
    
    handleOnSubmit(e) {
        const confirmFileName = e.target.elements["name"].value;
        
        if(this.fileName === confirmFileName) {
            this.props.onSubmit(e);
        } else {
            console.log(`Expected(${this.fileName} but found(${confirmFileName})`);
        }
        
        e.preventDefault();
    }
}    


