import React, { Component } from 'react';
import { TreeNode } from "./tree_node";

export class CreateForm extends Component {
    constructor(props) {
        super(props);
        
        this.handleOnNewFolder = this.handleOnNewFolder.bind(this);
        this.handleOnKeyDown = this.handleOnKeyDown.bind(this);
        this.state = {
            selected:null
        };
        this.pathRegex = /[^a-zA-Z0-9-_]/g;
    }
  
    render() {
        return (
            <form onSubmit={this.props.onSubmit} className="w3-container w3-margin-top branding-form" ref="newFileForm">
                <fieldset>
                    <legend>Choose Parent Folder</legend>
                    <div>
                        <TreeNode key={this.props.data.path} tree={this.props.data} selected={this.state.selected}/>
                    </div>
                    <div className="w3-margin-top">
                        <label htmlFor="newfolder" className="sr-only">Folder Name</label>
                        <input type="text" placeholder="New Folder Name" id="newfolder" ref="folderInput" onChange={this.handleOnKeyDown}/>
                        <input type="button" value="Create Folder" onClick={this.handleOnNewFolder}/>
                    </div>
                </fieldset>
                <div className="w3-margin-top">
                    <label htmlFor="name">File Name</label>
                    <input id="name" name="name" className="w3-input" type="text"/>
                </div>
                <div className="w3-margin-top">
                    <input type="submit" value="Create" className="w3-button w3-green" required={true}/>
                </div>
            </form>
        );
    }
    
    handleOnKeyDown(e) {
        e.target.value = e.target.value.replace(this.pathRegex,"").toLowerCase();
    }
    
    handleOnNewFolder() {
        let folderName = this.refs.folderInput.value;
        
        if(folderName !== "") {
            const parentFolder = this._findSelectedFolder(this.refs.newFileForm.elements),
                  parentTreeNode = this._findTreeNode(parentFolder.value, this.props.data);
           
            if(parentFolder && parentTreeNode) {
                let newTreeNode = {
                    children:[],
                    id:null,
                    leaf:false,
                    name:folderName,
                    path:parentTreeNode.path + folderName + "/",
                    root:false
                };
                
                parentTreeNode.children.push(newTreeNode);
                
                this.setState({
                   selected: newTreeNode.path
                });
                
                this.refs.folderInput.value = "";
            } else {
                console.log("parent item not found");
                console.log(parentFolder);
                console.log(parentTreeNode);
            }
        } else {
            console.log("no folder name");
        }
    }
    
    _findSelectedFolder(elements) {
        for(let i=0; i<elements.length; i++) {
            if(elements[i].name === "path" && elements[i].type === "radio" && elements[i].checked) {
                return elements[i];
            }
        }
    }
    
    _findTreeNode(needle, haystack) {
        if(!haystack.leaf) {
            if(haystack.path === needle) {
                return haystack;
            }
            
            for(let i=0; i<haystack.children.length; i++) {
                let child = this._findTreeNode(needle, haystack.children[i]);
                
                if(child) {
                    return child;
                }
            }
        }
    }
}    


