import React, { Component } from 'react';
import { FolderPicker } from "../../components/file_system";
import properties from "../../modules/app_properties";

export class FileSettingsForm extends Component {
    constructor(props) {
        super(props);
        
        this.pathRegex = properties.get("pathRegex");
    }
  
    render() {
        const lastSeparator = this.props.data.path.lastIndexOf("/")+1,
              path = this.props.data.path.substring(0, lastSeparator),
              name = this.props.data.path.substr(lastSeparator);
        
        return(
            <form onSubmit={this.props.onSubmit} className="w3-container w3-margin-top branding-form" ref="newFileForm">
                <FolderPicker key={this.props.tree.path} tree={this.props.tree} regex={this.pathRegex} selected={path}/>
                <div className="w3-margin-top">
                    <label htmlFor="name">File Name</label>
                    <input id="name" name="name" className="w3-input" type="text" defaultValue={name}/>
                </div>
                <div className="w3-margin-top">
                    <input type="submit" value="Save" className="w3-button w3-green" required={true}/>
                </div>
            </form>
        );
    }
}    


