import properties from './app_properties';

let wsWorker = new Worker('/js/worker/web_service_worker.js'),
    operations = {},
    requestCue = {},
    cueCount = 0,
    ready = true;
    
var doWork = function(cmd, data, callback){
    callback = (typeof callback === 'function') ? callback : function(){};


	if((ready || cmd === 'initialize') && cmd){ 
		if(cueCount === Number.MAX_SAFE_INTEGER){
			cueCount = 0;
		}
		
		let id = (cmd+cueCount++).split("").reduce(function(a,b){a=((a<<5)-a)+b.charCodeAt(0);return a&a;},0);
		
		requestCue[id] = {
		    cmd:cmd,
			fn:callback
		};				

		wsWorker.postMessage({
			id:id,
			cmd:cmd,
			data:data
		});
	}
};


wsWorker.addEventListener('message',function(e){
	var id = e.data.id,
		data = e.data.data,
		isChanged = e.data.isChanged;
	
	if(requestCue[id]){				
		let d = requestCue[id].fn(data);
		
		if(d !== false && isChanged === true){
		    let op = operations[requestCue[id].cmd];
		    
		    if(op){
		        d = d || data;
		        for(let i = 0; i < op.listeners.length; i++){ 
		            if(op.listeners[i](d) === false){
		                break;
		            }
		        }
		    }
		}
		
		delete requestCue[id];
	}
},false);

//Listen for properties to load
properties.on("load", () => {
   doWork("setBaseURL",properties.get("baseUrl"));
});


class WebServiceOperation {
    constructor(operation, method = "GET", endpoint) {
        doWork("initialize", {
            method:method,
            operation:operation,
            endpoint:endpoint
        });
        this.operationName = operation;
    }
    
    listen(callback){
        let op = operations[this.operationName];
        if(op && typeof callback === "function"){
            op.listeners.push(callback);
        }
    }
    
    request(data,callback){
        doWork(this.operationName, data, callback);
    }
    
    unlisten(callback){
        let op = operations[this.operationName];
        for(let i = 0; i < op.listeners.length; i++){
            if(op.listeners[i] === callback){
                op.listeners.splice(i,1);
            }
        }
    }
}

class WebServiceManager {
    
    createServiceOperation(operation, method, endpoint){
        if(!operations[operation]){
            operations[operation] = {
                op: new WebServiceOperation(operation, method, endpoint),
                listeners:[]
            };
        }
        
        return operations[operation].op;
    }
    
    
}

exports.webServiceManager = new WebServiceManager();


