import { webServiceManager } from '../../modules/web_service';
import properties from '../../modules/app_properties';

const getFilesOperation = webServiceManager.createServiceOperation("getFiles", "GET", `/rest/branding/files/{{projectId}}`),
      postNewFileOperation = webServiceManager.createServiceOperation("newFile", "POST", `/rest/branding/files/create`),
      getDeleteFileOperation = webServiceManager.createServiceOperation("deleteFile", "GET", `/rest/branding/files/delete/{{id}}`),
      getFileOperation = webServiceManager.createServiceOperation("getFile", "GET", `/rest/branding/files/edit/{{id}}`),
      postFileOperation = webServiceManager.createServiceOperation("postFile", "POST", `/rest/branding/files/edit/{{id}}`);

exports.getFiles = function(callback) {
    getFilesOperation.request({
        projectId:properties.get("projectId")
    }, callback);
};

exports.createFile = function(path, callback) {
    postNewFileOperation.request({
        projectId:properties.get("projectId"),
        path:path
    }, callback);
};

exports.deleteFile = function(id, callback) {
    getDeleteFileOperation.request({
        id:id
    }, callback);
};

exports.getFile = function(id, callback) {
    getFileOperation.request({
        id:id
    }, callback);
};

exports.postFile = function(data, callback) {
    postFileOperation.request(data, callback);
};

exports.listen = function(operation, callback){
    switch(operation){
        case "getStudent":
            getFilesOperation.listen(callback);
            break;
        default:
    }
};


