import React, { Component } from 'react';

export class TreeNode extends Component {
    
    render() {
        const sortedChildren = this.props.tree.children.sort((a,b) => {
            if(a.leaf !== b.leaf) {
                return a.leaf > b.leaf;
            } else {
                return a.name > b.name;
            }
        });
        
        const node = ((node) => {
        
            if(!node.leaf) {
                const isSelected = ((!this.props.selected && node.root) || node.path === this.props.selected);
                
                return (
                    <label htmlFor={node.path} className="w3-cell-row">
                        <div className="w3-cell branding-tree-radio">
                            <input type="radio" name="path" id={node.path} value={node.path} defaultChecked={isSelected} />
                        </div>
                        <div className="w3-cell">
                            <csit-folder title={node.name} onClick={this.props.onClick} open>
                                {
                                    sortedChildren.map((node) => {
                                        return <TreeNode key={node.path} tree={node} selected={this.props.selected}/>;
                                    })
                                }
                            </csit-folder>
                        </div>
                    </label>
                );
            }
            return null;
        })(this.props.tree);
        
        return (node);
    }
}


