import React from 'react';
import ReactDOM from 'react-dom';
import App from './app';
import history from './services/history';
import properties from './modules/app_properties';

const container = document.getElementById('app');

function render(container, location){               
 
    if(container) {
        ReactDOM.render(
            <App location={location}/>, container);
    }
}

function onHistory(location){
    render(container, location);
}

if(container) {
    
    properties.load({
      baseUrl:container.getAttribute("data-rest-url"),
      projectId:container.getAttribute("data-project-id")
    });
    
    render(container, history.location);
    history.listen(onHistory);
} else {
    alert("fatal error missing container element on page. Ensure an element with id equal to 'app' is present");
}


