import React, { Component } from 'react';

export class TreeNode extends Component {
    
    render() {
        const sortedChildren = this.props.tree.children.sort((a,b) => {
            if(a.leaf !== b.leaf) {
                return a.leaf > b.leaf;
            } else {
                return a.name > b.name;
            }
        });
        
        const node = ((node) => {
            if(node.leaf) {
                return <csit-file href={"/edit/" + node.id} title={node.name} onClick={this.handleOnClick}></csit-file>;
            } else {
                return (
                    <csit-folder title={node.name} onClick={this.props.onClick} open>
                        {
                            sortedChildren.map((node) => {
                                return <TreeNode key={node.path} tree={node}/>;
                            })
                        }
                    </csit-folder>
                );
            }
        })(this.props.tree);
        
        return (node);
    }
}


